@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;600;700;800;900&family=Noto+Serif+TC:wght@400;500;600;700;800&display=swap');
@font-face {
  font-family: 'WT002';
  src: url('./fonts/wt002.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'WT004';
  src: url('./fonts/wt004.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
}

img {
  user-drag: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
  html{
    @apply scroll-smooth;
    font-family: "Quicksand", 'Noto Sans TC',  sans-serif;
    overflow: hidden;
  }
  /*::-webkit-scrollbar {*/
  /*  -webkit-appearance: none;*/
  /*  display: none;*/
  /*}*/
    ::-webkit-scrollbar{
     width: 2px;
    }
    ::-webkit-scrollbar-thumb{
     border-radius: 4px;
       background-color: white;
    }
    ::-webkit-scrollbar-track-piece{
     background: #4f4f4f;
    }
  h1{@apply text-6xl font-bold}
  h2{@apply text-5xl font-bold}
  h3{@apply text-4xl font-bold}
  h4{@apply text-3xl font-bold}
  h5{@apply text-2xl font-bold}
  h6{@apply text-xl font-bold}
  .logoShadow{
    box-shadow: rgba(255,255,255,1) 0 0 50px 50px inset;
  }
}

:root {
  --vh: 1vh;
}

@media (min-width: 768px) {
  :root {
    --vh: calc(var(--vh, 1vh) * 100);
  }
}